import $http from './request.js'

//   /wechat/class/list
export const getClassList = ({pageSize, pageNum}) => {
  return $http.request({
    url: '/wechat/class/list',
    method: 'POST',
    header: {
      "Content-Type": "application/json",
    },
    data: {
      pageNum,
      pageSize
    }
  })
}
